.form-container > input {
  margin: 0;
  padding: 18px;
  background-color: white;
  border: 1px solid #d9d9d9;
  box-sizing: border-box;
  border-radius: 4px;
  display: block;
  width: 398px;
  height: 45px;
  margin-bottom: 25px;
}

.form-container > input:not(:first-child) {
  margin-bottom: 40px;
}

.title-login {
  text-align: left;
  margin-top: 20%;
  margin-bottom: 50px;
}
.btn-login {
  width: 264px;
  height: 45px;
  background: #0094ff;
  color: white;
  font-weight: bold;
  font-size: 18px;
  border: 1px solid #d9d9d9;
  box-sizing: border-box;
  border-radius: 4px;
  float: left;
}
